const setLocalStorage = ({ setSee, setClasses }) => {
  if (isEmptyInput(getByIdValue())) {
    addEntry();
    setClasses(JSON.parse(localStorage.getItem("allEntries")));
    setSee(false);
  } else {
    console.log("거짓 거짓");
    alert("모두 입력해주세요!!");
  }
};

const getByIdValue = () => {
  const subject = getLocationById(0);
  const professor = getLocationById(1);
  const days = getLocationById("dayOfWeek");
  const classRoom = getLocationById(3);
  const startTime = timeToFixed(getLocationById("startTime"));
  const endTime = timeToFixed(getLocationById("endTime"));

  return {
    subject,
    professor,
    days,
    classRoom,
    startTime,
    endTime,
    diffTime: endTime - startTime
  };
};

const addEntry = () => {
  var existingEntries = localStorage.getItem("allEntries");

  if (existingEntries == null) existingEntries = [];
  else existingEntries = JSON.parse(existingEntries);
  const entry = getByIdValue();
  existingEntries.push(entry);
  localStorage.setItem("allEntries", JSON.stringify(existingEntries));
};

const timeToFixed = time => {
  const hourMinute = time.split(":");
  return hourMinute[0] - 8 + hourMinute[1] / 60;
};

const isEmptyInput = ({
  subject,
  professor,
  days,
  classRoom,
  startTime,
  endTime
}) => {
  return subject && professor && days && classRoom && startTime && endTime;
};

const getLocationById = id => {
  return document.getElementById(id).value;
};

export default setLocalStorage;
