import React from "react";
import styled from "styled-components";

const days = { 1: "월", 2: "화", 3: "수", 4: "목", 5: "금", 6: "토", 7: "일" };

const SubjectInformation = ({ classInfo, canConstantSee, setConstantSee }) => {
  const getFloatingValue = time => {
    const normValue = Math.floor(time);

    const minute = Math.round((time - Math.floor(time)) * 60) / 100;
    console.log(String(normValue));
    console.log(String(minute));
    return String(normValue + 8) + ":" + String(minute * 100);
  };

  return (
    <Container onClick={() => setConstantSee(!canConstantSee)}>
      <Result>{classInfo.subject} </Result>
      <Result>{classInfo.professor} </Result>
      <Result>{classInfo.classRoom} </Result>
      <Result>{days[classInfo.days]} 요일</Result>
      <TimeZone>
        <Result>{getFloatingValue(classInfo.startTime)}시 </Result> ~
        <Result>{getFloatingValue(classInfo.endTime)}시</Result>
      </TimeZone>
    </Container>
  );
};

export default SubjectInformation;

const Container = styled.div`
  width: 200px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 150px;
  top: 200px;
  background-color: grey;
`;

const TimeZone = styled.div`
  display: flex;
  flex-direction: row;
`;

const Result = styled.div`
  color: white;
`;
