import React, { useState, Fragment, useEffect } from "react";
import styled from "styled-components";
import { ReactComponent as Plus } from "../components/plus.svg";
import Box from "../components/Box";
import setLocalStorage from "../components/setLocalStorage";
import SubjectInformation from "../components/SubjectInformation";

const Main = () => {
  const [classes, setClasses] = useState([]);
  const [canSee, setSee] = useState(false);
  const [canConstantSee, setConstantSee] = useState(false);
  const [classInfo, setClassInfo] = useState({});

  const firstRows = [
    ["", "월", "화", "수", "목", "금", "토", "일"],
    ["9시", "", "", "", "", "", "", ""],
    ["10시", "", "", "", "", "", "", ""],
    ["11시", "", "", "", "", "", "", ""],
    ["12시", "", "", "", "", "", "", ""],
    ["13시", "", "", "", "", "", "", ""],
    ["14시", "", "", "", "", "", "", ""],
    ["15시", "", "", "", "", "", "", ""],
    ["16시", "", "", "", "", "", "", ""],
    ["17시", "", "", "", "", "", "", ""],
    ["18시", "", "", "", "", "", "", ""]
  ];

  const inputValue = ["과목", "교수님", "요일", "강의실"];

  const addClassEvent = () => {
    setSee(!canSee);
  };

  useEffect(() => {
    setClasses(localStorage.getItem("allEntries"));
  }, [classes]);

  return (
    <Container>
      <button onClick={addClassEvent}>
        <Plus />
      </button>
      {canSee ? (
        <SetClassView canSee={canSee}>
          <h1>시간표 추가하기</h1>
          {inputValue.map((text, index) => (
            <InputBox value={text} id={index} key={index} />
          ))}
          <InputBox value={"시작시간"} type="time" id="startTime" />
          <InputBox value={"끝날시간"} type="time" id="endTime" />
          <Button onClick={() => setLocalStorage({ setSee, setClasses })}>
            등록
          </Button>
        </SetClassView>
      ) : (
        ""
      )}
      {canConstantSee ? (
        <SubjectInformation
          classInfo={classInfo}
          canConstantSee={canConstantSee}
          setConstantSee={setConstantSee}
        />
      ) : (
        ""
      )}
      <TableBox
        firstRows={firstRows}
        canSee={canSee}
        classes={classes}
        setClassInfo={setClassInfo}
        setConstantSee={setConstantSee}
        canConstantSee={canConstantSee}
      />
    </Container>
  );
};

const TableBox = ({
  firstRows,
  canSee,
  classes,
  setClassInfo,
  setConstantSee,
  canConstantSee
}) => {
  return (
    <TableView canSee={canSee}>
      {firstRows.map((row, heightIndex) => (
        <Rows>
          {row.map((value, rowIndex) => (
            <Box
              value={value}
              widthIndex={rowIndex}
              heightIndex={heightIndex}
              key={rowIndex}
              classes={classes}
              setClassInfo={setClassInfo}
              setConstantSee={setConstantSee}
              canConstantSee={canConstantSee}
            />
          ))}
        </Rows>
      ))}
    </TableView>
  );
};

const InputBox = ({ value, placeholder, type, id }) => {
  return (
    <Fragment>
      <InputContainer>
        {value}:
        {id !== 2 ? (
          <Input type={type} placeholder={placeholder} id={id} />
        ) : (
          <SelectInput />
        )}
      </InputContainer>
      <br />
    </Fragment>
  );
};

const SelectInput = () => {
  return (
    <select name="dayOfWeek" id="dayOfWeek">
      <option value="">요일선택</option>
      <option value="1">월</option>
      <option value="2">화</option>
      <option value="3">수</option>
      <option value="4">목</option>
      <option value="5">금</option>
      <option value="6">토</option>
      <option value="7">일</option>
    </select>
  );
};

const Input = styled.input``;

const InputContainer = styled.div`
  display: flex;
  flex-direction: row;
  font-size: 20px;
  font-weight: bold;
`;

const SetTimer = styled.div`
  display: flex;
  flex-direction: row;
`;

const Container = styled.div`
  width: 500px;
  height: 500px;
`;
const TableView = styled.div`
  display: flex;
  flex-direction: column;
  /* opacity: ${props => (props.canSee ? 0.5 : 1)}; */
`;

const Rows = styled.div`
  display: flex;
  flex-direction: row;
`;

const SetClassView = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  background-color: yellowgreen;
  border: 1px solid white;
  color: white;
  width: 300px;
  height: 400px;
  top: 110px;
  left: 110px;
`;

const Button = styled.button`
  width: 70px;
  height: 35px;
  font-size: 15px;
  background-color: white;
`;

export default Main;
