import React, { useState } from "react";
import styled from "styled-components";
import SubjectInformation from "./SubjectInformation";

const Box = ({
  value,
  widthIndex,
  heightIndex,
  classes,
  setClassInfo,
  setConstantSee,
  canConstantSee
}) => {
  if (classes != false) {
    try {
      const daysInf = JSON.parse(classes);
      for (let i = 0; i < daysInf.length; i++) {
        if (widthIndex == daysInf[i].days) {
          if (
            Math.floor(daysInf[i].startTime) < heightIndex &&
            Math.floor(daysInf[i].endTime) > heightIndex
          ) {
            console.log(1);
            return (
              <Container
                widthIndex={widthIndex}
                heightIndex={heightIndex}
                can={true}
                onClick={() => {
                  setClassInfo(daysInf[i]);
                  setConstantSee(!canConstantSee);
                }}
              />
            );
          } else if (Math.floor(daysInf[i].startTime) === heightIndex) {
            console.log(2);
            return (
              <Container
                widthIndex={widthIndex}
                heightIndex={heightIndex}
                can={false}
                left={true}
                leftPercent={
                  getPercent(
                    daysInf[i].startTime,
                    Math.floor(daysInf[i].startTime)
                  ) + "%"
                }
                rightPercent={
                  100 -
                  getPercent(
                    daysInf[i].startTime,
                    Math.floor(daysInf[i].startTime)
                  ) +
                  "%"
                }
                onClick={() => {
                  setClassInfo(daysInf[i]);
                  setConstantSee(!canConstantSee);
                }}
              >
                {daysInf[i].subject}
                {daysInf[i].classRoom}
              </Container>
            );
          } else if (Math.floor(daysInf[i].endTime) === heightIndex) {
            return (
              <Container
                widthIndex={widthIndex}
                heightIndex={heightIndex}
                can={false}
                left={false}
                leftPercent={
                  getPercent(
                    daysInf[i].endTime,
                    Math.floor(daysInf[i].endTime)
                  ) + "%"
                }
                rightPercent={
                  100 -
                  getPercent(
                    daysInf[i].endTime,
                    Math.floor(daysInf[i].endTime)
                  ) +
                  "%"
                }
                onClick={() => {
                  console.log(3);
                  setClassInfo(daysInf[i]);
                  setConstantSee(!canConstantSee);
                }}
              />
            );
          }
        }
      }
    } catch (e) {}
  }
  console.log("default");
  return (
    <Container widthIndex={widthIndex} heightIndex={heightIndex}>
      {value}
    </Container>
  );
};

const getPercent = (endTime, constant) => {
  return (endTime - constant).toFixed(2) * 100;
};

export default Box;

const Container = styled.div`
  width: 60px;
  height: ${props => (props.heightIndex === 0 ? "20px" : "60px")};
  border: 1px solid #f796b0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: ${props =>
    props.heightIndex === 0
      ? "pink"
      : props.widthIndex === 0
      ? "#fae5e9"
      : props.can
      ? "#b0f083"
      : props.left
      ? `linear-gradient(white 0% ${props.leftPercent}, #b0f083 ${
          props.leftPercent
        } 100%)`
      : `linear-gradient(#b0f083 ${props.leftPercent}, white ${
          props.leftPercent
        } ${props.rightPercent})`};

  font-weight: 600;
  font-size: ${props =>
    props.heightIndex != 0 && props.widthIndex != 0 ? "15px" : "15px"};
  color: white;
`;

const ViewInfo = ({ subject, professor, classRoom }) => {
  console.log("hiih");
  return (
    <SubjectInformation
      subject={subject}
      professor={professor}
      classRoom={classRoom}
    />
  );
};
